




















export default {
	data() {
		return {

		};
	},
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: "Sign in"
		});

	},

	methods: {
		login(res) {
			var formObject = res.detail.value;
			var username = formObject.username;
			var password = formObject.password;
			if (username.length == 0 || password.length == 0) {
				uni.showToast({
					title: 'Username or password can not be null',
				});
			} else {
				var that = this;
				uni.request({
					url: that.$serverUrl + '/login',
					method: 'POST',
					data: {
						username: username,
						password: password,
					},
					success: (res) => {
						console.log(res.data);
						var status = res.data.status;
						if (status == 200) {
							uni.showToast({
								icon: 'success',
								title: 'Welcome'
							});
							
							that.setGlobalUserInfo(res.data.data);
							console.log("login: userInfo.id=" + that.getGlobalUserInfo().id);
							console.log("login: userInfo.userToken=" + that.getGlobalUserInfo().userToken);
							
							that.goback();
						} else if (status == 500) {
							uni.showToast({
								icon: 'none',
								title: res.data.msg,
								duration: 3000
							});
						}
					}
				});
			}
		},

		register() {
			uni.navigateTo({
				url: '../register/register',
			})
		},

		goback() {
			uni.navigateBack({
				delta: 1
			})
		}
	},


}
